<template>
  <div id="app">
    <div class="app-wrapper">
      <div class="app-container">
        <div class="sidebar">
          <div class="sidebar-box">
            <a
              v-for="(item, index) in testData"
              :key="index"
              class="list-item"
              :href="'#'+item['title']"
            >{{item['title']}}</a>
          </div>
        </div>
        <div class="index-content">
          <Index :data="testData"></Index>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Index from "./components/Index";
export default {
  name: "app",
  components: {
    Index
  },
  data() {
    return {
      testData: [],
      code: ""
    };
  },
  created() {
    <div c></div>;
    this.fetchTestData();
  },
  methods: {
    processParams(_param) {
      let params = [];
      if (Array.isArray(_param)) {
        _param.forEach(_p => {
          let _ps = _p.split(":");
          params.push({
            name: _ps[0] || "",
            type: _ps[1] || "",
            explain: _ps[2] || "",
            remark: _ps[3] || "",
            other: _ps[4] || "",
            value: "" //存储输入值
          });
        });
      }
      return params;
    },

    filter(v) {
      console.log(v);
      if (v === "") {
        return false;
      }
      return true;
    },

    hasKey(_v) {
      if (_v === undefined || _v === null) {
        return false;
      }
      return true;
    },

    fetchTestData() {
      console.log(this.$doc_server);
      this.$http
        .get(`http://${this.$doc_server.addr}:${this.$doc_server.port}/doc/v1`)
        .then(res => {
          let body = res.data.Docs;
          console.log(body);
          if (body !== null && Array.isArray(body)) {
            body.forEach(e => {
              let params = e["Param"];
              params.forEach(p => {
                let param = {
                  title: this.hasKey(p["@title"]) ? p["@title"][0] : "",
                  url: this.hasKey(p["@url"]) ? p["@url"][0] : "",
                  header: this.hasKey(p["@header"]) ? p["@header"][0] : "",
                  method: this.hasKey(p["@method"]) ? p["@method"][0] : "",
                  params: this.processParams(p["@param"]),
                  result: "" //存储测试结果
                };
                this.filter(param["url"]) && this.testData.push(param);
              });
            });
          }
          console.log(this.testData);
        })
        .catch(err => {
          console.log(err);
        });
    },

    startTest(_i) {
      // 处理请求参数
      let params = {};
      this.testData[_i]["params"].forEach(_p => {
        params[`${_p["name"]}`] = _p["value"];
      });
      this.$http({
        url: `http://${this.$doc_server.apiServer}/${this.testData[_i]["url"]}`,
        method: this.testData[_i]["method"],
        header: {
          "Content-Type": "multipart/form-data"
        },
        params: params
      })
        .then(res => {
          this.testData[_i]["result"] = res.data;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

